import { resolveUserPathDefinitions, validatePathTranslations, prettyPrintPathDefinitionIssues, bestMatch, resolveRoute } from '../../../../../paraglide-js/dist/adapter-utils/index.js';
import { DEV } from '../../env.js';
import { rsc } from 'rsc-env';
import { availableLanguageTags, sourceLanguageTag } from '$paraglide/runtime.js';

/*
    Lingo:
    Canonical Path = Path without locale (how you write the href)
    Localised Path = Path with locale (how the path is visible in the URL bar)
*/
function PrefixStrategy({ pathnames: userPathnames, exclude, prefixDefault, prefixes, } = {}) {
    const resolvedPathnames =  userPathnames
        ? resolveUserPathDefinitions(userPathnames, availableLanguageTags)
        : {};
    const resolvedPrefixes = {
        ...Object.fromEntries(availableLanguageTags.map((lang) => [lang, lang])),
        ...prefixes,
    };
    if (DEV) {
        const withSlashes = Object.values(resolvedPrefixes).filter((val) => typeof val === "string" && val.includes("/"));
        if (withSlashes.length) {
            throw new Error(["[parglide-sveltekit:dev-only] Prefixes on cannot include slashes.", ...withSlashes].join("\n"));
        }
        const paths = Object.values(resolvedPathnames);
        const hasDuplicates = new Set(paths).size !== paths.length;
        if (hasDuplicates) {
            throw new Error("[parglide-sveltekit:dev-only] `prefixes` must be unique.");
        }
    }
    prefixDefault ??= "never";
    // Make sure the given pathnames are valid during dev
    // middleware is not rsc so validating there guarantees this will run once
    if (DEV && !rsc) {
        const issues = validatePathTranslations(resolvedPathnames, availableLanguageTags, {});
        if (issues.length) {
            console.warn("Issues were found with your pathnames. Fix them before deploying:\n\n" +
                prettyPrintPathDefinitionIssues(issues));
        }
    }
    function getCanonicalPath(localisedPath, locale) {
        // get the prefix for this language
        const prefix = resolvedPrefixes[locale] ?? locale;
        const pathWithoutLocale = localisedPath.startsWith(`/${prefix}/`)
            ? localisedPath.replace(`/${prefix}`, "")
            : localisedPath === `/${prefix}`
                ? "/"
                : localisedPath;
        for (const [canonicalPathDefinition, translationsForPath] of Object.entries(resolvedPathnames)) {
            if (!(locale in translationsForPath))
                continue;
            const translatedPathDefinition = translationsForPath[locale];
            if (!translatedPathDefinition)
                continue;
            const match = bestMatch(pathWithoutLocale, [translatedPathDefinition], {});
            if (!match)
                continue;
            return resolveRoute(canonicalPathDefinition, match.params);
        }
        return pathWithoutLocale;
    }
    function getTranslatedPath(canonicalPath, lang, translations) {
        const match = bestMatch(canonicalPath, Object.keys(translations), {});
        if (!match)
            return canonicalPath;
        const translationsForPath = translations[match.id];
        if (!translationsForPath)
            return canonicalPath;
        const translatedPath = translationsForPath[lang];
        if (!translatedPath)
            return canonicalPath;
        return resolveRoute(translatedPath, match.params);
    }
    return {
        getLocalisedUrl(canonicalPath, targetLanguage) {
            if (exclude && exclude(canonicalPath))
                return {
                    pathname: canonicalPath,
                };
            const translatedPath = getTranslatedPath(canonicalPath, targetLanguage, resolvedPathnames);
            const shouldAddPrefix = targetLanguage !== sourceLanguageTag || prefixDefault === "always";
            const prefix = resolvedPrefixes[targetLanguage] ?? targetLanguage;
            const localisedPath = shouldAddPrefix
                ? `/${prefix}${translatedPath == "/" ? "" : translatedPath}`
                : translatedPath;
            return {
                pathname: localisedPath,
            };
        },
        getCanonicalPath,
        resolveLocale(request) {
            const pathWithoutBase = request.nextUrl.pathname;
            const entries = Object.entries(resolvedPrefixes);
            const entry = entries.find(([, prefix]) => pathWithoutBase.startsWith(`/${prefix}/`) || pathWithoutBase === `/${prefix}`);
            const detected = entry?.[0];
            // If no prefix is detected and prefixDefault is "never" -> use default language
            // Otherwise leave it ambiguous
            if (prefixDefault === "never")
                return detected || sourceLanguageTag;
            return detected;
        },
    };
}

export { PrefixStrategy };
